import React from 'react';
import { Link } from 'react-router-dom';
import { Campo, Error, Formulario, InputSubmit } from './../ui/Formulario'
const SignUp = () => {
    return ( 
        <div className='login-pop'>
            <div>
                <img alt='welcomeImage' src='data:image/png;base64,'/>
            </div>
            <div>
                <h3>Registrate</h3>
            </div>
                
            <Formulario>   
                 <Error>El Campo es incorrecto</Error>
                <Campo>
                    <label htmlFor='email'>Correo Electronico</label>
                    <input id='email' type='text' />
                </Campo>
                <Campo>
                    <label htmlFor='nombre'>Nombre</label>
                    <input id='nombre' type='text' />
                </Campo>
                <Campo>
                    <label htmlFor='password'>Contraseña</label>
                    <input id='password' type='password' />
                </Campo>
                <InputSubmit type='submit' value='Registrar'/>
            </Formulario>
            <Link to='/sign-in'>Ya tines Cuenta?</Link>
        </div>
     );
}
 
export default SignUp;